* {
  box-sizing: border-box;
  margin: 0;
}

body {
  background-color: #efdfff;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

main {
  padding: 0 1em;
}

nav {
  background-image: linear-gradient(to bottom right, #9800c6, #ff2ca7);
  justify-content: center;
  align-items: center;
  height: 100%;
  min-height: 3.4em;
  max-height: 4em;
  display: flex;
}

.logo {
  max-width: 4em;
}

nav ul li {
  list-style-type: none;
  display: inline-block;
}

nav ul li a {
  color: #fff;
  padding: 1.1em 2em;
  font-size: 1.1em;
  font-weight: 500;
  text-decoration: none;
  transition: background-color .5s;
}

nav ul li a:hover {
  background-color: #9800c6;
}

h1 {
  letter-spacing: .5px;
  margin: 1em 0;
}

.h1center {
  color: #cc0d7c;
  text-transform: uppercase;
  justify-content: center;
  font-size: 3em;
  display: flex;
}

h2 {
  letter-spacing: .5px;
  margin: 1.5em 0 .5em;
}

p {
  letter-spacing: .4px;
  line-height: 1.5;
}

.hero-img {
  max-width: 1500px;
  height: 45em;
  display: flex;
}

.pil_small {
  display: none;
}

.hero-img img {
  z-index: -1;
  min-width: 20em;
  max-width: 60em;
  min-height: 30em;
  margin: 8em 1em 1em 30%;
  position: absolute;
}

.info1 {
  text-align: center;
  background-image: linear-gradient(to bottom right, #cc0d7c, #9800c6);
  border: 10px solid #ff2ca7;
  border-radius: 50%;
  min-width: 19em;
  max-width: 30em;
  height: 100%;
  max-height: 30em;
  margin: 2em 10%;
  padding: 11em 3em;
}

.info1 strong {
  color: #fff;
  letter-spacing: .5px;
  font-size: 1em;
  line-height: 1.5;
}

@media screen and (max-width: 1092px) {
  .info1 {
    margin: 2em 1em;
  }

  .hero-img img {
    margin-left: 10%;
  }
}

.info2 {
  color: #000;
  background-color: #fff;
  border: 10px solid #ff2ca7;
  width: 100%;
  max-width: 40em;
  height: auto;
  margin: 2em auto;
  padding: 1em 3em 5em;
}

ul {
  margin: 1em 0;
}

li {
  letter-spacing: .4px;
  margin: 1em 0;
}

.bild img {
  border: 7pt solid #ff2ca7;
  border-radius: 50%;
}

.foto-container {
  background-image: url("bakgrund_bilder.ed3cd127.svg");
  grid-template: "bild1 ." 1fr
                 "bild1 bild2" 1fr
                 "bild3 bild2" 1fr
                 "bild3 bild4" 1fr
                 "bild5 bild4" 1fr
                 "bild5 ." 1fr
                 / 1fr 1fr;
  gap: 0;
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
}

.bild1 {
  grid-area: bild1;
  margin: 2em auto;
}

.bild2 {
  grid-area: bild2;
  margin: 4em auto;
}

.bild3 {
  grid-area: bild3;
  margin: 4em auto;
}

.bild4 {
  grid-area: bild4;
  margin: 4em auto;
}

.bild5 {
  grid-area: bild5;
  margin: 2em auto;
}

.foto-container picture img {
  width: 100%;
}

.flex-container {
  justify-content: center;
  align-items: center;
  margin-bottom: 5em;
  display: flex;
}

.process-container {
  background-color: #fff;
  border: 7px solid #ff2ca7;
  width: 100%;
  max-width: 700px;
  padding: 1em;
}

footer {
  color: #fff;
  background-image: linear-gradient(to bottom right, #9800c6, #ff2ca7);
  align-items: center;
  height: 100%;
  min-height: 5em;
  padding: 0 5em;
  display: flex;
}

.hamburger {
  cursor: pointer;
  -webkit-appearance: none;
  appearance: none;
  z-index: 10;
  float: right;
  background: none;
  border: none;
  outline: none;
  width: 50px;
  margin: -45px 1em 0;
  display: block;
}

.hamburger .bar, .hamburger:after, .hamburger:before {
  content: "";
  z-index: 10;
  background-color: #fff;
  border-radius: 5px;
  width: 100%;
  height: 4px;
  margin: 4px 0;
  transition: all .4s;
  display: block;
  position: relative;
}

.hamburger.active:before {
  transform: rotate(-45deg)translate(-9px, 6px);
}

.hamburger.active .bar {
  opacity: 0;
}

.hamburger.active:after {
  transform: rotate(45deg)translate(-5px, -3px);
}

.mobile_nav {
  z-index: 1;
  background-color: #9800c6;
  width: 100%;
  min-height: 100vh;
  padding-top: 120px;
  transition: all .4s;
  display: block;
  position: fixed;
  top: 0;
  left: 100%;
}

.mobile_nav.active {
  left: 0;
}

.mobile_nav li {
  text-align: center;
  padding: 12px 16px;
  display: block;
}

@media screen and (min-width: 599px) {
  .hamburger {
    display: none;
  }
}

@media screen and (max-width: 880px) {
  .hero-img {
    justify-content: center;
    height: 42em;
  }

  .hero-img .pil_large {
    display: none;
  }

  .hero-img img {
    width: 100%;
    height: 60%;
    margin: 180px 0 0;
    display: flex;
    position: absolute;
  }

  .example img {
    width: 100%;
    max-width: 512px;
    margin-top: 1em;
  }
}

@media screen and (max-width: 600px) {
  .large_nav {
    display: none;
  }

  footer {
    padding: 0 1em;
  }

  .info2 {
    border: 6px solid #ff2ca7;
    padding: 1em .5em 5em;
  }

  .info1 {
    border: 6px solid #ff2ca7;
    width: 24em;
    height: 24em;
    margin: 2em 2%;
    padding: 7em 2em;
  }

  .hero-img {
    height: 35em;
  }

  .hero-img img {
    width: 100%;
    height: 30%;
    margin: 80px 0 0;
  }

  .bild img {
    border: 4pt solid #ff2ca7;
  }

  .h1center {
    font-size: 1.8em;
  }
}

@media screen and (max-width: 450px) {
  .info1 {
    width: 21em;
    height: 21em;
    margin: 2em 2%;
    padding: 4em 1em;
    font-size: .8em;
  }

  .hero-img {
    height: 30em;
  }

  .hero-img img {
    width: 100%;
    height: 10%;
    max-height: 30px;
    margin: 0;
  }
}

